.ladingpage-treatment {
	.breadcrumb {
		margin-bottom: 50px;
	}

	.page-title {
		text-transform: uppercase;
	}

	.sapo {
		margin-bottom: 24px;
	}

	.archive-title {
		text-transform: none;
	}
}

.page-qoute {
	margin-bottom: 50px;

	.quote-img {
		display: block;
	}

	.quote-bottom {
		margin-bottom: 15px;
	}

	.quote-bottom {
		position: relative;
		left: 95%;
	}

	.qoute-item {
		padding: 15px;
		width: 75%;
		margin: 0 auto;
		font-size: 20px;
		font-weight: 700;
		border-radius: 10px;
		box-shadow: 0 3px 6px rgba($color: #000000, $alpha: .16);

		&:not(:last-child) {
			margin-bottom: 16px;
		}
	}
}

.box-rule {
	padding: 16px;
	border-radius: 10px;
	background: #E6F3ED;
	margin-bottom: 50px;

	.title {
		font-weight: 700;
		font-size: 16px;
		margin-bottom: 16px;
	}

	.list-rule {
		list-style: none;
		padding-left: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 0;

		.item {
			width: 26%;
			margin-bottom: 8px;

			img {
				margin-right: 8px;
			}
		}
	}
}

.list-method-treatment {
	.inner {
		margin-bottom: 16px;
		padding: 21px;
		background: #E6F3ED;
		border-radius: 10px;
		margin-bottom: 16px;
	}

	.thumbnail {
		width: 90px;
		height: 90px;
		border-radius: 50%;
		overflow: hidden;

		img {
			object-fit: cover;
			object-position: 50% 50%;
			-o-object-position: 50% 50%;
			width: 100%;
			height: 100%;
		}
	}
}